import React from 'react'
import Layout from '../components/layout'
import Banner from '../components/banner'
import SEO from '../components/seo'
import BootstrapTable from 'react-bootstrap-table-next'
import ToolkitProvider, { Search } from 'react-bootstrap-table2-toolkit'
import OrgIcon from '../../assets/menu_icon_agencies.svg'
import { useTranslation } from 'react-i18next'
import { Link } from 'gatsby'
import '../styles/table.css'
const { SearchBar } = Search

export default ({ pageContext: { fobaiTable } }) => {

  const { t, i18n } = useTranslation()

  return (
      <Layout>
        <SEO title={ t('organizations.fobai.title') } lang={ i18n.language } />
        <Banner title={ t('organizations.fobai.title') }
          subtitle={ t('organizations.fobai.subtitle') }
          image='https://images.bible.cloud/fab/banners/agencies.jpg'
          icon={OrgIcon}
          subnav={[
            {path: '/members/', title: t('organizations.fobai.title')},
            {path: '/organizations/', title: t('organizations.title')}
          ]}
          breadcrumbs={[
            {path: '/', title: 'home'},
            {path: '/organizations/', title: t('organizations.title')}
          ]}
        />
        <div className="row">
          <div className="medium-8 columns centered thead-hide fobai-table">
            <ToolkitProvider
                keyField="id"
                data={ fobaiTable }
                bordered={ false }
                columns={[
                  { dataField: 'id', text: 'id', hidden: true, keyField: true },
                  { dataField: 'name',
                    text: 'English Name',
                    sort: true,
                    formatter: (cell, row) => <Link to={`/organizations/${row.slug}`} title={cell}>
                      <div dangerouslySetInnerHTML={{__html: row.logo }}></div>
                    </Link>
                  }
                ]}
                search
            >
              {
                props => (
                    <div className="squares">
                      <SearchBar { ...props.searchProps } delay={ 100 } placeholder={t('search.title') + " " + fobaiTable.length + " " + t('organizations.title')} />
                      <BootstrapTable { ...props.baseProps } />
                    </div>
                )
              }
            </ToolkitProvider>
          </div>
        </div>

      </Layout>
  )
}